.DividerThree{
    height: auto;
    background-color: #008e8e;
    border-top: 3px solid #5a5a5a;
    text-align: center;
    color: white;
    margin: 0 auto;
    padding: 20px 0;
    overflow: hidden;
}

.divider-content-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.choice{
    color: white;
}

.arrow{
    width: 30px;
    height: 30px;
    margin: 0 20px;
}

.choice-container{
    height: auto;
    text-align: center;
    font-family: 'Cabin', sans-serif;
    font-size: 2.5em;
}

.social-media-container{
    text-align: center;
}

.social-media{
    height: 50px;
    width: 50px;
    margin: 0 10px;
}

.social-media:hover{
    cursor: pointer;
}

.hiring{
    font-size: 1.2em;
    line-height: 1.4em;
}

#resume-icon, #mail-icon{
    border-radius: 5px;
}

.yellow-underline{
    color: yellow;
    text-decoration: underline;
}

.me-info{
    margin: 5px 0;
}

@media screen and (max-width: 480px) {
    .choice{
        font-size: 0.8em;
    } 

    .arrow{
        width: 20px;
        height: 20px;
        margin: 0 5px;
    }
    .social-media{
        margin: 5px 10px;
    }

}

@media screen and (max-width: 370px) {
    .choice{
        font-size: 0.6em;
    } 

    .arrow{
        width: 15px;
        height: 15px;
    }
}


