.sectionOne {
    background-image: url("../../images/SectionOne/kevin-computer-blue-reduced-twice-1336.jpg");
    height: 100%;
    max-height: 1080px;
    min-height: 640px;
    width: 100%;
    background-color:#3d6f78;
    overflow: auto;
}

.Section-One-Container{
    position: relative;
    background-attachment: scroll;
    background-size: cover;
    max-width: 1920px;
    min-height: 640px;
    height: 100%;
    margin: 0 auto;
}

.title-tag-container{
    position: absolute;
    bottom: 200px;
    background-image:url("../../images/common/black-opaque.png");
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    color: white;
    font-size: 3em;
    width: 620px;
    padding: 15px 15px 25px 0px;

}

.kc{
    margin-left: 10%;
}

@media screen and (max-width: 1350px) {
    .sectionOne{
        background-position: 30% 0%;
    }  
}



@media screen and (max-width: 890px) {
    .title-tag-container{
        font-size: 30px;
        width: 100%;
        text-align: center;
        padding: 15px 0 25px 0;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    .kc{
        margin-left: 0%;
    }

    .sectionOne{
        background-position: 56% 50%;
    }
}




