@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css");


.DividerOne{
    height: auto;
    padding: 30px 0;
    margin: 0 auto;
    overflow: hidden;
}

.Divider-One-Container{
    max-width: 1218px;
    margin: 0 auto;
}

h2{
    font-size: 2.5em;
    text-align: center;
}

.divider-p{
    font-size: 1.3em;
    color: #58547D;
    text-indent: 2em;
    margin-top: 20px;
    line-height: 1.5em;
}

.my-pic{
    height: 140px;
    width: 140px;
}

.skills-container{
    /* width: 800px; */
    /* min-width: 1100px; */
    /* display: flex; */
    /* justify-content: space-evenly; */
    text-align: center;
}

.skill{
    width: 80px;
    height: auto;
    margin: 0 10px;
}



#mySQL-skill{
    border-radius: 15px;
}

.about-me{
    margin-top: 20px;
}

.red{
    color: red;
}

.blue{
    color: blue;
}

.shield, .hardhat, .webdev, .awesome{
    height: 0.8em;
    width: auto;
}

.my-pic-backup{
    display: none;
}

@media screen and (max-width: 1140px) {

.skill{
    width: 70px;
    height: auto;
    margin: 0 10px;
    position: relative;
    bottom: 30px;
}

/* we originally set a min width to this property above */
/* .skills-container{
    min-width: 0;
} */



}

@media screen and (max-width: 945px) {

.backup-container{
    text-align: center;
    margin: 0 auto;
}

.my-pic-backup{
    text-align: center;
    display: inline;
    height: 140px;
    width: 140px;
    margin-bottom: 10px;
}

.skill{
    position: static;
    margin-top: 15px;
}

.my-pic{
    display: none;
}

/* .skills-container{
    min-width: 0;
} */
    
}
    
@media screen and (max-width: 360px) {
    .divider-p{
        text-align: center;
        text-indent: 0;
    }
}


