.DividerTwo{
    height: auto;
    overflow: hidden;
    /* background-color: #ffffff; */
    background-color:white;
    /* min-width: 620px; */
}

.Divider-Two-Container{
    max-width: 1218px;
    margin: 0 auto;
}

.investment-container{
    text-align: center;
    margin-top: 20px;
}

.investment{
    width: 250px;
    height: auto;
    border-radius: 15px;
    border: 2px solid black;
}

.investment-title{
    margin: 20px;
}


.investment-icon{
    width: 100px;
    height: 100px;
}

.analysis-container{
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 25px;
}

.point-container{
    width: 350px;
    margin: 0 15px 0;
}

.phrase{
    font-size: 1.6em;
    margin: 10px 0;
}

.explanation{
    line-height: 1.4em;
}

@media screen and (max-width: 570px) {
    .analysis-container{
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .point-container{
        margin: 10px auto;
    }
}

@media screen and (max-width: 370px) {
    .investment{
        width: 200px;
    }

    .point-container{
        width: 225px;
    }
}


