.sectionTwo {
    background-image: url("../../images/SectionTwo/kevin-phone-red-reduced.jpg");
    overflow: hidden;
    padding: 30px;
    height: auto;
}

.Section-Two-Container{
    max-width: 1218px;
    margin: 0 auto;
}

.another{
    color: white;
    font-size: 3em;
    background-image:url("../../images/common/black-opaque.png");
    width: 620px;
    padding: 15px;
    padding-bottom: 25px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.portfoliowrapper{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: center;
        align-content: center;
    height: auto;
    margin: 0 auto;
    max-width: 1450px;
    /* min-width: 560px; */
    max-width: 1536px;
}



.actual-image:hover, .portfolioimages:hover{
    cursor: pointer;
}


.image-tag{ /*image tags*/
	position:relative;	
	margin:0;
	padding:0;
	color:white;
	font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
	font-size: .85em;
	height:45px;
	background-color:#4aaaa5;
	bottom:80px;
	line-height:44px;
	text-align:center;
	opacity:.9;
    width: 100%;
    font-size: 1.3em;
    font-weight: 500;
}

.another{
    text-align: center;
    width: 680px;
    border-radius: 30px;
    margin: 0 auto 0;
    padding-bottom: 0;
    padding: 15px;
}


@media screen and (max-width: 820px) {
    .another{
        width: 400px;
    }   

    .sectionTwo{
        background-position: 62% 50%;
    }
}


.ck{
    margin-left: 0;
}

.another-container{
    text-align: center;
    /* min-width: 560px; */
}

.portfolioimages{
    margin-top: 30px;
    width: 380px;
    height: 285px;
}

.actual-image{
    width: 100%;
    height: 100%;
    border-radius: 20px;
}

@media screen and (max-width: 1330px) {
    
        .portfolioimages{
            width: 320px;
            height: 240px;
        }
    
}

@media screen and (max-width: 1075px) {
    
        .portfolioimages{
            width: 256px;
            height: 192px;
        }
    
}

@media screen and (max-width: 540px) {
    
        .another{
            width: 90%;
        }
    
        .ck{
            font-size: 0.9em;
        }

}

@media screen and (max-width: 300px){
    .image-tag{
        font-size: 0.95em;
        font-weight: 900px;
    }
}


