


.sectionThree {
    background-image: url("../../images/SectionThree/kevin-brick-green-reduced.jpg");
    height: 70%;
    max-height: 1080px;
    min-height: 640px;
    width: 100%;
    background-color:#3d6f78;
    overflow: auto;
    background-position: 50% 50%; 
}



 .Section-Three-Container{
    background-attachment: fixed;
    background-size: 1920px;
    max-width: 1920px;
    min-height: 640px;
    width: 100%;
    margin: 0 auto;
} 

    
@media screen and (max-width: 1920px) {
    
        .Section-Three-Container{
            background-size: cover;
        }
}


